* {
  box-sizing: border-box;
}

button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.navbar {
  font-size: 2rem;
  padding: 1em;
  background-color: #03bafc;
}

.navbar-logo {
  color: blue;
  margin-right: 0.5em;
}

.navbar-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  background-color: blue;
  color: white;
  border-radius: 50%;
}

.add-input {
  font-size: 2rem;
}

.add-input {
  margin-right: 0.2rem;
}

.add-button {
  height: 100%;
  font-size: 1.5rem;
  padding: 0.3em 0.5em;
  background-color: #03bafc;
  color: white;
}

.habit {
  font-size: 2.5rem;
  padding: 0.5em;
  list-style: none;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 3rem;
  height: 3rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: #03bafc;
  color: white;
  border-radius: 50%;
}

.habit-button {
  font-size: 2.5rem;
  margin: 0 0.1em;
  background: transparent;
}

.habit-button:hover {
  opacity: 0.8;
}

.habit-increase,
.habit-decrease {
  color: blue;
}

.habit-delete {
  font-size: 2rem;
  vertical-align: baseline;
  color: black;
}

.habits-reset {
  font-size: 1.5rem;
  padding: 0.2em 1em;
  background-color: #03bafc;
  color: white;
}
